.header{
    width: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    
}
.header h1 {
    color: white;
    text-align: center;

}

.header h4 {

    text-align: center;
    color: #15f4ee;
    font-size: small;

}

.header hr {
    position: relative;
background-color:white;
    width: 70%;
    height:20%;
   margin: 6px auto;
}

.header span hr {
    position: absolute;
    width: 2%;
    height: 2%;
    border: none;
    background-color: rgb(255, 102, 0);
    margin-top: -10px;
    margin-left: -15px;
}

